<template>
  <div class="treatmentlist">
    <v-card class="mt-2">
      <v-card-title>
        <v-row>
          <v-col cols="12" md="8">
            <h2 class="subheading grey--text">
              <v-icon class="mx-1">fas fa-file-alt</v-icon>
              <span>
                {{this.$store.state.uData.p_code}} /
                {{this.$store.state.uData.name}}
              </span>
              <span>個案服務處遇計畫表</span>
            </h2>
          </v-col>
          <v-col cols="12" md="4">
            <!-- 功能表 -->
            <v-toolbar dense>
              <v-text-field
                hide-details
                prepend-icon="mdi-magnify"
                single-line
                label="搜尋"
                v-model="serachKey"
                @keyup.stop="search()"
              ></v-text-field>

              <popupadd
                @getAllData="getAllData"
                ref="editProcess"
              ></popupadd>
            </v-toolbar>
          </v-col>
        </v-row>
      </v-card-title>
      <v-card-text>
        <v-card class="mt-2">
          <v-card-text>
            <!-- 表單內容 -->
            <v-data-iterator
              :items="viewData"
              :items-per-page.sync="itemsPerPage"
              :footer-props="{ itemsPerPageOptions }"
            >
              <template v-slot:default="props">
                <v-simple-table fixed-header class="mt-2 text-no-wrap">
                  <template v-slot:default>
                    <thead style="background-color: #e3f2fd;">
                      <tr>
                        <th></th>
                        <th>評估日期</th>
                        <th>致障原因</th>
                        <th>生活自理能力</th>
                        <th>經濟來源</th>
                        <th>家庭支持功能</th>
                        <th>服務對象問題及需求</th>
                        <th>服務目標</th>
                        <th>服務策略-執行方法</th>
                        <th>服務策略-服務資源</th>
                        <th>服務策略-執行期程</th>
                        <th>服務策略-執行者</th>
                        <th>評值</th>
                        <th>紀錄人</th>
                        <th>修改紀錄</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(item,index) in props.items" :key="index">
                        <td>
                          <v-menu transition="scale-transition" offset-y>
                            <template v-slot:activator="{ on }">
                              <v-btn color="primary" dark v-on="on">功能</v-btn>
                            </template>
                            <v-list>
                              <v-list-item @click="edit(item)">
                                <v-list-item-title>修改</v-list-item-title>
                              </v-list-item>
                              <v-list-item @click="del(item)">
                                <v-list-item-title>刪除</v-list-item-title>
                              </v-list-item>
                              <v-list-item @click.stop="print(item)">
                                <v-list-item-title>列印</v-list-item-title>
                              </v-list-item>
                            </v-list>
                          </v-menu>
                        </td>
                        <td>{{item.date}}</td>
                        <td>{{item.obstaclereason}}</td>
                        <td>{{item.selfcareability}}</td>
                        <td>{{item.economicsources}}</td>
                        <td>{{item.familysupport}}</td>
                        <td>{{item.qa}}</td>
                        <td>{{item.target}}</td>
                        <td>{{item.means}}</td>
                        <td>{{item.resources}}</td>
                        <td>{{item.schedule}}</td>
                        <td>{{item.executor}}</td>
                        <td>{{item.evaluation}}</td>

                        <td>{{item.create_man}}</td>
                        <td>{{item.edit_man}}</td>
                      </tr>
                    </tbody>
                  </template>
                </v-simple-table>
              </template>
            </v-data-iterator>
          </v-card-text>
        </v-card>
      </v-card-text>
    </v-card>

    <!-- <pre>{{items}}</pre> -->
  </div>
</template>

<script>
import qs from "qs";
import popupadd from "@/views/document/TreatmentAdd";
import dayjs from "dayjs";

export default {
  components: { popupadd },
  data() {
    return {
      items: [], //目前讀取進來的資料庫
      viewData: [], //顯示的資料
      serachKey: "",
      edititem: {}, //修改中資料
      itemsPerPageOptions: [10, 20, 30],
      itemsPerPage: 10
    };
  },
  mounted() {
    this.getAllData();
  },
  methods: {
    getAllData() {
      var postData = {
        key: "u_snkey",
        value: this.$store.state.uData.snkey
      };

      //取得所有資料
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post(
          "general/getByKey/" + this.$store.state.databaseName + "/treatment",
          qs.stringify(postData)
        )
        .then(rs => {
          this.items = this.viewData = rs.data.map(i=>{
            var newItem = JSON.parse(i.datalist);
            newItem['snkey'] = i.snkey;
            newItem['create_man'] = i.create_man;
            newItem['edit_man'] = i.edit_man;
            return newItem;
          });
          // this.$store.commit("setLoadingOff"); //讀取畫面off
          this.sortBy();
        });
    },
    sortBy() {
      //依name排序
      this.viewData = this.viewData.sort(function(a, b) {
        return parseInt(a.date) < parseInt(b.date) ? 1 : -1;
      });
    },
    edit(item) {
      //修改功能
      this.$refs.editProcess.$emit("fromParent",item);
    },
    del(delData) {
      //刪除功能
      this.$confirm("確認刪除?").then(res => {
        delData.delman =
          this.$store.state.pData.username +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")";
        var postData = {
          snkey:delData.snkey,
          tablename:'treatment',
          info: JSON.stringify(delData),
        };

        if (res) {
          this.axios
            .post(
              "general/delv3/" + this.$store.state.databaseName + "/treatment",
              qs.stringify(postData)
            )
            .then(rs => {
              console.log(rs.data);
              if (rs.data.state == 1) {
                var pop = { msg: "已刪除", type: true, theme: "success" };
                this.$store.commit("snackbar", pop);
                this.getAllData();
              }
            });
        }
      });
    },
    search() {
      var searchArr = this.serachKey.split(" ");
      var outputitems = [...this.items];
      var str = "";
      searchArr.forEach(key => {
        if (key != "") {
          outputitems = outputitems.filter(item => {
            str = JSON.stringify(item).toUpperCase();
            if (str.includes(key.toUpperCase())) {
              return item;
            }
          });
        }
      });
      this.viewData = outputitems;
    },
    print(item) {
      // var printData = {
      //   medicalData: item,
      //   medicineData: medicineData
      // };

      item.pic_url = this.$store.state.base_url+'upload/familytree/'+this.$store.state.uData.pic_url_familytree;

      //送出資料到列印頁面
      sessionStorage.setItem("printData", JSON.stringify(item));
      let routeData = this.$router.resolve({
        path: "/print/treatmentprint"
      });
      window.open(routeData.href, "_blank");
    },
  }
};
</script>


<style lang="scss">
.treatmentlist {
  h2 {
    span {
      vertical-align: middle;
    }
  }
  thead {
    th {
      font-size: 1.2rem;
      font-family: "微軟正黑體";
      color: red;
    }
  }
  tbody {
    td {
      font-size: 1rem;
    }
  }
}
</style>