<template>
  <div class="treatmentadd">
    <v-dialog v-model="dialog" width="auto">
      <template v-slot:activator="{ on }">
        <v-icon color="red lighten-2" dark v-on="on" @click="addProcess">mdi-plus-circle</v-icon>
      </template>

      <v-card>
        <v-card-title :class="titleStyle" primary-title>{{title}}</v-card-title>

        <v-card-text>
          <v-form ref="form" class="mt-3">
            <v-card>
              <v-card-text>
                <v-col cols="3">
                  <v-text-field
                    label="評估日期"
                    v-model="list.date"
                    type="date"
                    :rules="emptyRules"
                    hide-details
                  ></v-text-field>
                </v-col>
                <v-col cols="9"></v-col>
                <v-col cols="12">
                  <v-alert color="primary" outlined>
                    <h3>一、住民基本資料</h3>
                    <v-row>
                      <v-col cols="3">床號:{{userData.bed_name}}</v-col>
                      <v-col cols="3">入住日期:{{userData.in_date}}</v-col>
                      <v-col cols="3">姓名:{{userData.name}}</v-col>
                      <v-col cols="3">性別:{{userData.sex}}</v-col>
                      <v-col cols="3">生日:{{userData.birthday}}</v-col>
                      <v-col cols="9">身分別:{{ filteridentity }}</v-col>
                      <v-col cols="3">障礙類別:{{ userData.obstacle.length ? $store.state.uData.obstacle[0].kind : "" }}</v-col>
                      <v-col cols="3">障礙等級:{{ userData.obstacle.length ? $store.state.uData.obstacle[0].lv : "" }}</v-col>
                      <v-col cols="3">到期日:{{ userData.obstacle.length ? $store.state.uData.obstacle[0].date : "" }}</v-col>
                      <v-col cols="3">教育程度:{{ filtereducation }} </v-col>
                      <v-col cols="4">
                        <v-select
                          :items="obstaclereasonItems"
                          label="致障原因"
                          v-model="list.obstaclereason"
                        ></v-select>
                      </v-col>
                    </v-row>
                  </v-alert>
                </v-col>

                <v-col cols="12">
                  <v-alert color="primary" outlined>
                    <h3>二、家系圖</h3>
                    <input type="file" ref="fileInput" @change="onFileSelected" style="display:none" />
                    <v-img
                    :src="pic_url"
                    :lazy-src="this.$store.state.base_url+'upload/familytree/lazypic.jpg'"
                    class="grey lighten-2"
                    height="500px"
                    contain
                  >
                    <v-progress-circular color="white" indeterminate size="32" v-if="uploading"></v-progress-circular>
                    <v-btn
                      icon
                      class="success ma-1 float-right"
                      @click.stop="uploadId=$store.state.uData.snkey;uploadFolder='familytree';$refs.fileInput.click()"
                    >
                      <v-icon class="white--text">mdi-cloud-upload</v-icon>
                    </v-btn>
                  </v-img>
                  </v-alert>
                </v-col>

                <v-col cols="12">
                  <v-alert color="primary" outlined>
                    <h3>三、社會心理功能診斷分析</h3>
                    <v-row>
                      <v-col cols="4">
                        <v-combobox
                          :items="selfcareabilityItems"
                          label="1.生活自理能力"
                          v-model="list.selfcareability"
                        ></v-combobox>
                      </v-col>
                      <v-col cols="4">
                        <v-text-field
                          label="2.生活適應能力-適應評估表(分)"
                          :value="filteradaptability"
                          outlined
                          readonly
                        ></v-text-field>
                      </v-col>
                      <v-col cols="4">
                        <v-text-field
                          label="3.情緒狀況-憂鬱量表(分)"
                          :value="filtermelancholy"
                          outlined
                          readonly
                        ></v-text-field>
                      </v-col>
                      <v-col cols="4">
                        <v-combobox
                          :items="economicsourcesItems"
                          label="4.經濟來源"
                          v-model="list.economicsources"
                        ></v-combobox>
                      </v-col>
                      <v-col cols="4">
                        <v-combobox
                          :items="familysupportItems"
                          label="5.家庭支持功能"
                          v-model="list.familysupport"
                        ></v-combobox>
                      </v-col>
                    </v-row>
                  </v-alert>
                </v-col>

                <v-col cols="12">
                  <v-alert color="primary" outlined>
                    <h3>四、個案處遇計畫</h3>
                    <v-textarea label="服務對象問題及需求" v-model="list.qa"></v-textarea>
                    <v-textarea label="服務目標" v-model="list.target"></v-textarea>
                    <v-alert color="success" outlined>
                      <v-sheet color="primary white--text" v-model="list.strategy">服務策略</v-sheet>
                      <v-textarea label="執行方法" v-model="list.means"></v-textarea>
                      <v-textarea label="服務資源" v-model="list.resources"></v-textarea>
                      <v-textarea label="執行期程" v-model="list.schedule"></v-textarea>
                      <v-text-field label="執行者" v-model="list.executor"></v-text-field>

                    </v-alert>
                    <v-textarea label="評值" v-model="list.evaluation"></v-textarea>
                  </v-alert>
                </v-col>
              </v-card-text>
            </v-card>
          </v-form>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="primary" @click="addOK" v-if="processType=='add'" :loading="loading" :disabled="loading">確認新增</v-btn>
          <v-btn class="success" @click="editOK" v-if="processType=='edit'" :loading="loading" :disabled="loading">確認修改</v-btn>
        </v-card-actions>
        <!-- <pre>{{nursingassessmentItems}}</pre>  -->
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import qs from "qs";
import dayjs from "dayjs";

export default {
  data() {
    return {
      dialog: false,
      list: {
        snkey: "",
        date: ""
      }, //表單內資料
      oldList: {}, //未修改前的表單內資料
      processType: "", //存放頁面執行是add新增或edit修改
      title: "",
      titleStyle: "",
      emptyRules: [v => !!v || "不可空白"],
      age: dayjs(this.$store.state.uData.birthday).format("YYYY") - 1911,
      adaptabilityItems: [],
      melancholyItems: [],
      nursingassessmentItems: [],
      obstaclereasonItems: [
        "先天(出身即有)",
        "後天疾病",
        "老年退化",
        "交通事故",
        "職業傷害",
        "其他事故傷害",
        "家庭或社會環境因素"
      ],
      selfcareabilityItems: ["完全依賴", "部分依賴", "可自理"],
      economicsourcesItems: ["子女提供", "退休俸或積蓄", "政府補助","單位資助","親戚朋友資助","其他"],
      familysupportItems: ["良好", "尚可", "不佳"],
      
      selectedFile: "", //選擇的上傳檔案資料
      uploadId: "", //上傳檔案後的回傳ID
      uploadFolder:"",  //暫存上傳檔案的種類 (病歷的大頭貼user或家族樹 familytree)
      fd: new FormData(), //上傳的檔案及文案內容
      uploading: false, //控制上傳中的動畫
      pic_url:"",
      loading:false,
    };
  },

  mounted() {
    //父組件要執行本組件時的function時的設定 (showFromParent) id是暫存傳過來的值
    this.$on("fromParent", function(item) {
      this.dialog = true;
      this.editProcess(item);
    });

    //取得適應性評估表,憂鬱量表,入院護理評估
    let readDatas = ['adaptability','melancholy','nursingassessment']
    readDatas.forEach(i=>{
      this.getData(i) 
    });
    // this.getAdaptability(); //取得適應性評估表
    // this.getMelancholy(); //取得憂鬱量表
    // this.getNursingassessment(); //取得入院護理評估
    this.pic_url = this.$store.state.base_url+'upload/familytree/'+this.$store.state.uData.pic_url_familytree;
  },

  computed:{
    //回傳使用者
    userData(){
      return this.$store.state.uData;
    },
    //回傳適應性分數
    filteradaptability() {
      if (this.adaptabilityItems.length > 0) {
        var matchData = this.adaptabilityItems.filter(i => {
          return i.date == this.list.date;
        });
        if (matchData.length > 0) {
          var data = JSON.parse(matchData[0].datalist);
          return data.total;
        }else{
          return '未設定日期或暫無符合資料';
        }
      }else{
        return '未設定日期或暫無符合資料';
      }
    },
    //回傳憂鬱分數
    filtermelancholy() {
      if (this.melancholyItems.length > 0) {
        var matchData = this.melancholyItems.filter(i => {
          return i.date == this.list.date;
        });
        if (matchData.length > 0) {
          var data = JSON.parse(matchData[0].datalist);
          return data.total;
        }else{
          return '未設定日期或暫無符合資料';
        }
      }else{
        return '未設定日期或暫無符合資料';
      }
    },
    //回傳身分別
    filteridentity() {
      if (this.nursingassessmentItems.length > 0) {
        let item = this.nursingassessmentItems[0];

        let rs = "";
        if (item.identity1 == "true") {
          rs += "一般;";
        }
        if (item.identity2 == "true") {
          rs += "中低收入戶;";
        }
        if (item.identity3 == "true") {
          rs += "低收入(一款)";
        }
        if (item.identity4 == "true") {
          rs += "低收入(二款)";
        }
        if (item.identity5 == "true") {
          rs += "低收入(三款)";
        }
        if (item.identity6 == "true") {
          rs += "原住民;";
        }
        if (item.identity7 == "true") {
          rs += "榮民(榮眷);";
        }
        if (item.identity8 == "true") {
          rs += "其他:";
        }
        if (item.identity9 && item.identity9 != "") {
          rs += item.identity9 + ";";
        }
        return rs;
      }
    },//回傳教育程度
    filtereducation() {
      if (this.nursingassessmentItems.length > 0) {
        var item = this.nursingassessmentItems[0];
        var rs = "";
        if (item.education1 == "true") {
          rs += "不識字;";
        }
        if (item.education2 == "true") {
          rs += "國小以下;";
        }
        if (item.education3 == "true") {
          rs += "小學";
        }
        if (item.education4 == "true") {
          rs += "國中";
        }
        if (item.education5 == "true") {
          rs += "高中(職)";
        }
        if (item.education6 == "true") {
          rs += "大學/大專;";
        }
        if (item.education7 == "true") {
          rs += "大學以上;";
        }
        if (item.education8 == "true") {
          rs += "不確定;";
        }

        return rs;
      }
    },
  },

  methods: {
    getData(dataTable) {
      var postData = {
        key: "u_snkey",
        value: this.$store.state.uData.snkey
      };

      //取得所有資料
      this.$api.options( "general/getByKey/" + this.$store.state.databaseName + "/" + dataTable, postData )
        .then(rs => { this[dataTable + 'Items'] = rs });
    },
    // getAdaptability() {
    //   var postData = {
    //     key: "u_snkey",
    //     value: this.$store.state.uData.snkey
    //   };

    //   //取得所有資料
    //   this.$api.options(
    //       "general/getByKey/" +
    //         this.$store.state.databaseName +
    //         "/adaptability",postData
    //     )
    //     .then(rs => {
    //       this.adaptabilityItems = rs
    //     });
    // },
    // getMelancholy() {
    //   var postData = {
    //     key: "u_snkey",
    //     value: this.$store.state.uData.snkey
    //   };

    //   //取得所有資料
    //   //this.$store.commit("setLoadingOn"); //讀取畫面on
    //   this.axios
    //     .post(
    //       "general/getByKey/" + this.$store.state.databaseName + "/melancholy",
    //       qs.stringify(postData)
    //     )
    //     .then(rs => {
    //       this.melancholyItems = rs.data;
    //       // this.$store.commit("setLoadingOff"); //讀取畫面off
    //     });
    // },
    // getNursingassessment() {
    //   var postData = {
    //     key: "u_snkey",
    //     value: this.$store.state.uData.snkey
    //   };

    //   //取得所有資料
    //   this.$api.options("general/getByKey/" + this.$store.state.databaseName + "/nursingassessment", postData)
    //     .then(rs => {
    //       this.nursingassessmentItems = rs.map(i=>JSON.parse(i.datalist));
    //     });
    // },
    addProcess() {
      //進入畫面時,執行的是新增作業
      this.processType = "add";
      this.title = "新增資料";
      this.titleStyle = "font-weight-black error lighten-2";

      this.list.snkey = "";
      this.list.date = "";
    },
    editProcess(item) {
      //進入畫面時,執行的是修改作業
      this.processType = "edit";
      this.title = "修改資料";
      this.titleStyle = "font-weight-black success lighten-2";
      this.list = { ...item }; //複制一份實際修改資料內容
    },
    addOK() {
      //確認新增
      if (this.$refs.form.validate()) {
        var postData = {
          u_snkey:this.$store.state.uData.snkey,
          date:this.list.date,
          datalist:JSON.stringify(this.list),
          create_man:this.$store.state.pData.username + "(" + this.$store.state.time + ")", //執行動作的人,時間,內容
        }

        this.loading = true;
        this.$api.options( "general/add/" + this.$store.state.databaseName + "/treatment",postData )
          .then(rs => {
            if (rs.state == 1) {
              var pop = { msg: "已新增", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.$emit("getAllData");
              this.dialog = false;
              this.loading = false;
            }
          });
      } else {
        var pop = { msg: "資料輸入不完全!!請重新確認!!", type: true, theme: "warning"};
        this.$store.commit("snackbar", pop);
      }
    },
    editOK() {
      //確認修改
      if (this.$refs.form.validate()) {
        this.$store.commit("getTime"); //取得現在時間
        var postData = {
          snkey:this.list.snkey,
          date:this.list.date,
          datalist:JSON.stringify(this.list),
          edit_man:this.$store.state.pData.username + "(" + this.$store.state.time + ")", //執行動作的人,時間,內容
        }

        this.loading = true;
        this.axios
          .post(
            "general/edit/" + this.$store.state.databaseName + "/treatment",
            qs.stringify(postData)
          )
          .then(rs => {
            if (rs.data.state == 1) {
              var pop = { msg: "已修改", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.dialog = false;

              for (var i in this.list) {
                if (this.oldList[i] == undefined){
                  this.$set(this.oldList, i , "");
                }
                //將各個欄位的資料透過一對一的去比對,就可以使vue對資料會有反應
                this.oldList[i] = this.list[i];
              }
              this.loading = false;
            }
          });
      } else {
        var pop = {
          msg: "資料輸入不完全!!請重新確認!!",
          type: true,
          theme: "warning"
        };
        this.$store.commit("snackbar", pop);
      }
    },
        onFileSelected(event) {
      //選取圖片
      this.selectedFile = event.target.files[0];
      var extension = this.selectedFile.name
        .split(".")
        .pop()
        .toLowerCase(); //取副檔名
      var fitType = ["gif", "png", "jpg", "jpeg"];

      var errorMsg = ""; //判斷檔案是否為圖檔及檔案大小
      if (fitType.indexOf(extension) == -1) {
        errorMsg = "檔案:" + this.selectedFile.name + "不是支持的影像檔案";
      }
      if (this.selectedFile.size > 1024 * 1024 * 5) {
        //檔案大小設定-最大5MB
        errorMsg = "檔案:" + this.selectedFile.name + "不能超過5M唷";
      }

      if (errorMsg == "") {
        this.onUpload();
      } else {
        var pop = { msg: errorMsg, type: true, theme: "warning" };
        this.$store.commit("snackbar", pop);
        this.selectedFile = "";
      }
    },
    onUpload() {
      this.uploading = true;
      //執行上傳
      this.fd.append("snkey", this.uploadId); //放入上傳圖檔的id
      this.fd.append("folder", this.uploadFolder); //放入上傳圖檔的種類 (user / familytree)
      this.fd.append("image", this.selectedFile);

      this.axios
        .post(
          "general/upload/" + this.$store.state.databaseName + "/user",
          this.fd
        )
        .then(rs => {
          //更新圖
          this.pic_url = this.$store.state.base_url+'upload/familytree/'+rs.data.success;
          // if (this.uploadFolder == 'thumb'){
          //   this.oldList.pic_url = this.list.pic_url = rs.data.success;
          // }
          // if (this.uploadFolder == 'familytree'){
          //   this.oldList.pic_url_familytree = this.list.pic_url_familytree = rs.data.success;
          // }
          this.uploading = false;
        });
    },
  }
};
</script>
